import { fundFAQData } from 'https://lukasemperfi.github.io/space-lab/data/fund-page/fund-faq.min.js';

class FundFAQSection extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.innerHTML = `
      <section class="fund-faq faq">
        <div class="faq__container">
          <div class="faq__col1">
            <h2 class="faq__title">Frequently Asked Questions</h2>
            <video 
                class="faq__video" 
                muted 
                loop 
                playsinline      
                autoplay
            >
                <source src="https://lukasemperfi.github.io/space-lab/assets/video/faq.mp4" type="video/mp4">
                Your browser does not support the video tag.
            </video>
          </div> 
          <div class="faq__col2">
            <faq-accordion></faq-accordion> 
          </div>

          <img loading="lazy"  class="fund-faq__decor-right-desktop" src="https://lukasemperfi.github.io/space-lab/assets/images/fund-faq/decor-right-desktop.svg" alt="fund-faq-bg" class="faq__bg">
        </div>
      </section>
    `;

    requestAnimationFrame(() => {
      this.passDataToAccordion();
    });
  }

  passDataToAccordion() {
    const accordion = this.querySelector('faq-accordion');
    if (accordion && typeof accordion.setQuestions === 'function') {
      accordion.setQuestions(fundFAQData);
    } else {
      console.error(
        'faq-accordion не найден или метод setQuestions недоступен'
      );
    }
  }
}

customElements.define('fundfaq-section', FundFAQSection);
